'use client'

import { useState } from 'react'
import { Menu, X } from 'lucide-react'
import styles from './Header.module.css'

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen)
  }

  return (
    <header className={styles.header}>
      <div className={styles.container}>
        <div className={styles.nav}>
          {/* Logo */}
          <div className={styles.logo}>
            <h1 className={styles.logoText}>단체주문</h1>
          </div>

          {/* Desktop Navigation */}
          <nav className={styles.desktopNav}>
            <a href="#home" className={styles.navLink}>
              홈
            </a>
            <a href="#restaurants" className={styles.navLink}>
              음식점
            </a>
            <a href="#how-it-works" className={styles.navLink}>
              이용방법
            </a>
            <a href="#support" className={styles.navLink}>
              고객지원
            </a>
          </nav>

          {/* Auth Buttons - Desktop */}
          <div className={styles.authButtons}>
            <a href="/login" className={styles.loginButton}>
              로그인
            </a>
            <a href="/signup" className={styles.signupButton}>
              회원가입
            </a>
          </div>

          {/* Mobile menu button */}
          <button onClick={toggleMenu} className={styles.mobileMenuButton}>
            {isMenuOpen ? (
              <X className="block h-6 w-6" aria-hidden="true" />
            ) : (
              <Menu className="block h-6 w-6" aria-hidden="true" />
            )}
          </button>
        </div>
      </div>

      {/* Mobile Navigation Menu */}
      {isMenuOpen && (
        <div className={styles.mobileMenu}>
          <div className={styles.mobileNavContainer}>
            <a
              href="#home"
              className={styles.mobileNavLink}
              onClick={() => setIsMenuOpen(false)}
            >
              홈
            </a>
            <a
              href="#restaurants"
              className={styles.mobileNavLink}
              onClick={() => setIsMenuOpen(false)}
            >
              음식점
            </a>
            <a
              href="#how-it-works"
              className={styles.mobileNavLink}
              onClick={() => setIsMenuOpen(false)}
            >
              이용방법
            </a>
            <a
              href="#support"
              className={styles.mobileNavLink}
              onClick={() => setIsMenuOpen(false)}
            >
              고객지원
            </a>
            <div className={styles.mobileAuthButtons}>
              <a
                href="/login"
                className={styles.mobileLoginButton}
                onClick={() => setIsMenuOpen(false)}
              >
                로그인
              </a>
              <a
                href="/signup"
                className={styles.mobileSignupButton}
                onClick={() => setIsMenuOpen(false)}
              >
                회원가입
              </a>
            </div>
          </div>
        </div>
      )}
    </header>
  )
}